:root {
 --md-sys-color-primary: var(--md-sys-color-primary-light);
 --md-sys-color-on-primary: var(--md-sys-color-on-primary-light);
 --md-sys-color-primary-container: var(--md-sys-color-primary-container-light);
 --md-sys-color-on-primary-container: var(--md-sys-color-on-primary-container-light);
 --md-sys-color-secondary: var(--md-sys-color-secondary-light);
 --md-sys-color-on-secondary: var(--md-sys-color-on-secondary-light);
 --md-sys-color-secondary-container: var(--md-sys-color-secondary-container-light);
 --md-sys-color-on-secondary-container: var(--md-sys-color-on-secondary-container-light);
 --md-sys-color-tertiary: var(--md-sys-color-tertiary-light);
 --md-sys-color-on-tertiary: var(--md-sys-color-on-tertiary-light);
 --md-sys-color-tertiary-container: var(--md-sys-color-tertiary-container-light);
 --md-sys-color-on-tertiary-container: var(--md-sys-color-on-tertiary-container-light);
 --md-sys-color-error: var(--md-sys-color-error-light);
 --md-sys-color-error-container: var(--md-sys-color-error-container-light);
 --md-sys-color-on-error: var(--md-sys-color-on-error-light);
 --md-sys-color-on-error-container: var(--md-sys-color-on-error-container-light);
 --md-sys-color-background: var(--md-sys-color-background-light);
 --md-sys-color-on-background: var(--md-sys-color-on-background-light);
 --md-sys-color-surface: var(--md-sys-color-surface-light);
 --md-sys-color-on-surface: var(--md-sys-color-on-surface-light);
 --md-sys-color-surface-variant: var(--md-sys-color-surface-variant-light);
 --md-sys-color-on-surface-variant: var(--md-sys-color-on-surface-variant-light);
 --md-sys-color-outline: var(--md-sys-color-outline-light);
 --md-sys-color-inverse-on-surface: var(--md-sys-color-inverse-on-surface-light);
 --md-sys-color-inverse-surface: var(--md-sys-color-inverse-surface-light);
 --md-sys-color-inverse-primary: var(--md-sys-color-inverse-primary-light);
 --md-sys-color-shadow: var(--md-sys-color-shadow-light);
 --md-sys-color-surface-tint: var(--md-sys-color-surface-tint-light);
 --md-sys-color-outline-variant: var(--md-sys-color-outline-variant-light);
 --md-sys-color-scrim: var(--md-sys-color-scrim-light);
 --md-sys-color-scrim-rgb: var( --md-sys-color-scrim-light-rgb);

 /* extended */

 --md-sys-color-surface-1: rgb(var(--md-sys-color-primary-light-rgb) / 0.05);
 --md-sys-color-surface-2: rgb(var(--md-sys-color-primary-light-rgb) / 0.08);
 --md-sys-color-surface-3: rgb(var(--md-sys-color-primary-light-rgb) / 0.11);
 --md-sys-color-surface-4: rgb(var(--md-sys-color-primary-light-rgb) / 0.12);
 --md-sys-color-surface-5: rgb(var(--md-sys-color-primary-light-rgb) / 0.14);

 --md-sys-elevation-0: 0px 0px 0px 0px rgb(var(--md-sys-color-shadow-light-rgb) / 0.2), 0px 0px 0px 0px rgb(var(--md-sys-color-shadow-light-rgb) / 0.14), 0px 0px 0px 0px rgb(var(--md-sys-color-shadow-light-rgb) / 0.12);
 --md-sys-elevation-1: 0px 3px 1px -2px rgb(var(--md-sys-color-shadow-light-rgb) / 0.2), 0px 2px 2px 0px rgb(var(--md-sys-color-shadow-light-rgb) / 0.14), 0px 1px 5px 0px rgb(var(--md-sys-color-shadow-light-rgb) / 0.12);
 --md-sys-elevation-2: 0px 2px 4px -1px rgb(var(--md-sys-color-shadow-light-rgb) / 0.2), 0px 4px 5px 0px rgb(var(--md-sys-color-shadow-light-rgb) / 0.14), 0px 1px 10px 0px rgb(var(--md-sys-color-shadow-light-rgb) / 0.12);
 --md-sys-elevation-3: 0px 5px 5px -3px rgb(var(--md-sys-color-shadow-light-rgb) / 0.2), 0px 8px 10px 1px rgb(var(--md-sys-color-shadow-light-rgb) / 0.14), 0px 3px 14px 2px rgb(var(--md-sys-color-shadow-light-rgb) / 0.12);
 --md-sys-elevation-4: 0px 5px 5px -3px rgb(var(--md-sys-color-shadow-light-rgb) / 0.2), 0px 8px 10px 1px rgb(var(--md-sys-color-shadow-light-rgb) / 0.14), 0px 3px 14px 2px rgb(var(--md-sys-color-shadow-light-rgb) / 0.12);
 --md-sys-elevation-5: 0px 8px 10px -6px rgb(var(--md-sys-color-shadow-light-rgb) / 0.2), 0px 16px 24px 2px rgb(var(--md-sys-color-shadow-light-rgb) / 0.14), 0px 6px 30px 5px rgb(var(--md-sys-color-shadow-light-rgb) / 0.12);


}
